import { useState, useRef, useEffect, FC, FormEvent, ChangeEvent } from "react";
import {
	Input,
	Button
} from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./ForgotPasswordPage.module.css";
import { Link, useNavigate } from "react-router-dom";
import { sendPasswordResetThunk } from "../../services/reducers/auth";
import { useAppDispatch } from "../..";

export const ForgotPasswordPage: FC = () => {
	const [email, setEmail] = useState<string>("");
	const inputEmail = useRef<HTMLInputElement>(null);
	const navigate = useNavigate();
	const dispatch = useAppDispatch();

	useEffect(() => {
		inputEmail.current && inputEmail.current.focus();
	}, []);

	const onSubmit = async (e: FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		try {
			await dispatch(sendPasswordResetThunk({ email }));
			navigate("/reset-password");
		} catch (error) {
			console.error("  :", error);
		}
	};

	return (
		<main className={styles.main}>
			<h1 className="text text_type_main-medium"> </h1>
			<form className={styles.form} onSubmit={onSubmit}>
				<Input
					ref={inputEmail}
					type={"email"}
					placeholder=" e-mail"
					onChange={(e: ChangeEvent<HTMLInputElement>) =>
						setEmail(e.target.value)
					}
					value={email}
					name={"email"}
					error={false}
					errorText={".   email"}
					size={"default"}
					extraClass="ml-1"
					autoComplete={"email"}
				/>
				<Button htmlType="submit" type="primary" size="medium">
					
				</Button>
			</form>
			<p className="text text_type_main-default text_color_inactive">
				 ?{" "}
				<Link className={styles.link} to="/login">
					
				</Link>
			</p>
		</main>
	);
};
